import React from 'react';
import './TodoItem.css';

import { faTimesCircle, faEdit } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

class TodoItem extends React.Component {
  constructor(props) {
    super(props)
  
    this.state = {
       title:'',
       id:''
    }
  }
  
  render() {
    const { id, title } = this.props.todo;

    
    return (
      <ul>
        <input type="text" key={id} name="title" value={title} onChange = {e => this.props.editTitle(e.target.value, id)}  />
        {/* <li>{title}{' '}</li> */}
        <span><i className="times-icon">
          <FontAwesomeIcon
            icon={faTimesCircle}
            onClick={this.props.delTodo.bind(this, id)}
          />
        </i></span>
        
        <i className="times-icon">
          <FontAwesomeIcon
            icon={faEdit}
            onClick={this.props.editTitle.bind(this, id)}
          />
        </i>
      </ul>
    );
  }
}

export default TodoItem;
